import {
  HeaderContainer,
  Title,
  WarStatistic,
  Text,
  Date,
  Wrap,
  Subtitle,
} from "./Header.styled";

export const Header = ({ children, day }) => {
  return (
    <HeaderContainer>
      <Subtitle>   </Subtitle>
      <Title>    </Title>
      <Wrap>
        <Text>
             <Date>2022-02-24</Date>  {children}{" "}
        </Text>
        {day&&<WarStatistic>{day}-  </WarStatistic>}
      </Wrap>
    </HeaderContainer>
  );
};
